import Aurora from '../page/Aurora/index'
import Home from '../page/Aurora/Home/index'
import Article from '../page/Aurora/Article/index'

import Hesperis from '../page/Hesperis/index'

export default new VueRouter({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'aurora',
      component: Aurora,
      children:[{
        path: '/',
        name: 'home',
        component: Home,
      },{
        path: '/article',
        name: 'article',
        component: Article,
      }]
    },
    {
      path: '/admin',
      name: 'admin',
      component: Hesperis
    }
  ]
})
