<template>
    <div class="yu-wrapper">
        <img :src="value" class="yu-wrapper__img" alt="lhlyu"
             :style="{
                height:height,
                width:width
             }">
    </div>
</template>

<script>
    export default {
        name: "Wrapper",
        props:{
            img:{
               type: String,
                validator(val){},
                default:"https://cdn.jsdelivr.net/gh/lhlyu/pb@master/2019/1.png"
            },
            height:{
                type: String,
                default: "100%"
            },
            width:{
                type: String,
                default: "100%"
            }
        },
        data(){
          return {
              value:"https://cdn.jsdelivr.net/gh/lhlyu/pb@master/2019/2.png"
          }
        },
        async mounted(){
            this.loadImg()
        },
        methods:{
            loadImg(){
                if(this.img){
                    var loadimg = new Image();
                    loadimg.src = this.img;
                    let self = this
                    loadimg.onload = function(){
                        self.value = loadimg.src
                    }
                }
            }
        }
    }
</script>