<template>
    <div class="yu-card" ref="yc" :style="flex ? `flex: ${flex}`:``">
        <slot></slot>
    </div>
</template>

<script>
    export default {
        name: "Card",
        props:{
            flex:{
                type: String,
                default: null
            }
        },
        async mounted () {
            if(!this.$isMobile){
                this.$nextTick(() => {
                    this.scroll()
                    window.addEventListener('scroll', this.scroll)
                })
            }else{
                this.opacity()
            }
        },
        destroyed () {
            if(!this.$isMobile) {
                window.removeEventListener('scroll', this.scroll, false)
            }
        },
        methods:{
            scroll(){
                const elOffsetTop = this.$refs.yc.offsetTop
                const docScrollTop = document.documentElement.scrollTop - 230
                if (elOffsetTop < (docScrollTop + window.innerHeight)) {
                    this.$refs.yc.style.animation = "slideInUp 0.5s linear"
                    this.$refs.yc.style.webkitAnimation = "slideInUp 0.5s linear"
                    window.removeEventListener('scroll', this.scroll, false)
                    this.$refs.yc.style.opacity = "1"
                }
            },
            opacity(){
                this.$refs.yc.style.opacity = "1"
            }
        }
    }
</script>