<template>
        <div :class="[`yu-nail`,
                     `yu-nail--${position}`]">
            <span :style="`background-color: ${color}`">
                <slot></slot>
            </span>
        </div>
</template>

<script>
    export default {
        name: "Nail",
        props:{
            position:{
                type: String,
                default: 'rt',
                validator (value) {
                    return ['rt',"rb","lt","lb"].includes(value);
                },
            },
            color:{
                type: String,
                default: 'rgba(46, 204, 113,0.6)'
            }
        }
    }
</script>

