<template>
  <div id="app">
    <vue-progress-bar></vue-progress-bar>
    <router-view></router-view>
    <ScrollTop></ScrollTop>
  </div>
</template>

<script>
import ScrollTop from './components/ScrollTop/index'

export default {
  name: 'app',
  components: {
    ScrollTop,
  },
  created() {
    this.initProgress()
  },
  mounted() {
    this.$Progress.finish()
  },
  methods: {
    // 注册顶部进度条
    initProgress() {
      this.$Progress.start()
      this.$router.beforeEach((to, from, next) => {
        this.$Progress.start()
        next()
      })
      this.$router.afterEach(() => {
        this.$Progress.finish()
      })
    }
  }
}
</script>

