<template>
    <header class="yu-header" ref="header">
        <div class="yu-header__nav">
            <Cacia></Cacia>
        </div>
    </header>
</template>

<script>

    import Cacia from '../Cacia/index'

    export default {
        name: "Header",
        components:{
            Cacia
        },
        data(){
            return {
                lastScrollTop:0
            }
        },
        async mounted () {
            this.$nextTick(() => {
                this.scroll()
                window.addEventListener('scroll', this.scroll)
            })
        },
        methods:{
            scroll(){
                const scrolltop = document.documentElement.scrollTop
                if(scrolltop > this.lastScrollTop){
                    this.$refs.header.style.display = 'none'
                }else{
                    this.$refs.header.style.display = 'block'
                }
                this.lastScrollTop = scrolltop
            }
        }
    }
</script>